"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import {
  Box,
  Button,
  Flex,
  Stack,
  Heading,
  Text,
  Input,
  IconButton,
  HStack,
  Link as ChakraLink,
  Field,
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { LuEye, LuEyeOff, LuMail, LuLock } from "react-icons/lu";
import { FaGoogle, FaGithub } from "react-icons/fa";
import { useMutation } from "@tanstack/react-query";
import { useDispatch } from "react-redux";
import { setUser } from "../store/authSlice";
import { toaster } from "@/components/ui/toaster"; // Import toaster utility

// Use motion.create for v3 compatibility
const MotionBox = motion.create(Box);

export default function LoginPage() {
  const router = useRouter();
  const dispatch = useDispatch();

  const [showPassword, setShowPassword] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const loginMutation = useMutation({
    mutationFn: async (data: { email: string; password: string }) => {
      const res = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      });
      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || "Login failed");
      }
      return res.json();
    },
    onSuccess: (data: any) => {
      dispatch(setUser(data.user));
      // Chakra v3 Toaster usage
      toaster.create({
        title: "Welcome back!",
        description: "Successfully signed in.",
        type: "success",
      });
      router.push("/dashboard");
    },
    onError: (error: any) => {
      toaster.create({
        title: "Login failed",
        description: error.message,
        type: "error",
      });
    },
  });

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    loginMutation.mutate({ email, password });
  };

  return (
    <Flex
      minH="100vh"
      align="center"
      justify="center"
      bg="gray.50"
      _dark={{ bg: "gray.950" }}
      p={{ base: 4, md: 8 }}
    >
      <MotionBox
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.4, ease: "easeOut" }}
        style={{ width: "100%", maxWidth: "480px" }}
      >
        <Box
          p={{ base: 6, md: 10 }}
          bg="white"
          _dark={{ bg: "gray.900" }}
          boxShadow="2xl"
          borderRadius="3xl"
          borderWidth="1px"
          borderColor="gray.100"
          _darkBorderColor="gray.800"
          width="100%"
        >
          <Stack gap={8} width="100%">
            <Stack align="center" textAlign="center" gap={2}>
              <Heading size="4xl" fontWeight="black" letterSpacing="tight">
                Sign In
              </Heading>
              <Text color="gray.500" fontSize="lg">
                Access your personalized dashboard.
              </Text>
            </Stack>

            <Stack gap={3} width="100%">
              <Button variant="outline" size="xl" width="100%" gap={3} borderRadius="2xl">
                <FaGoogle color="#EA4335" /> Google
              </Button>
              <Button variant="outline" size="xl" width="100%" gap={3} borderRadius="2xl">
                <FaGithub /> GitHub
              </Button>
            </Stack>

            <HStack width="100%">
              <Box flex="1" h="1px" bg="gray.100" _dark={{ bg: "gray.800" }} />
              <Text fontSize="xs" color="gray.400" fontWeight="bold" whiteSpace="nowrap" px={2}>
                OR LOGIN WITH EMAIL
              </Text>
              <Box flex="1" h="1px" bg="gray.100" _dark={{ bg: "gray.800" }} />
            </HStack>

            <Box as="form" onSubmit={handleLogin} width="100%">
              <Stack gap={5}>
                <Field.Root disabled={loginMutation.isPending}>
                  <Field.Label fontWeight="bold" mb={1}>Email</Field.Label>
                  <Box position="relative">
                    <Box position="absolute" left="4" top="50%" transform="translateY(-50%)" color="gray.400" zIndex={1}>
                      <LuMail size={20} />
                    </Box>
                    <Input
                      type="email"
                      placeholder="name@example.com"
                      ps="12"
                      size="xl"
                      borderRadius="2xl"
                      variant="subtle"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      required
                    />
                  </Box>
                </Field.Root>

                <Field.Root disabled={loginMutation.isPending}>
                  <HStack justify="space-between" width="100%">
                    <Field.Label fontWeight="bold" mb={1}>Password</Field.Label>
                    <ChakraLink fontSize="xs" color="teal.600" fontWeight="bold">
                      Forgot?
                    </ChakraLink>
                  </HStack>
                  <Box position="relative">
                    <Box position="absolute" left="4" top="50%" transform="translateY(-50%)" color="gray.400" zIndex={1}>
                      <LuLock size={20} />
                    </Box>
                    <Input
                      type={showPassword ? "text" : "password"}
                      placeholder="••••••••"
                      ps="12"
                      pe="12"
                      size="xl"
                      borderRadius="2xl"
                      variant="subtle"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      required
                    />
                    <IconButton
                      aria-label="Toggle password"
                      variant="ghost"
                      position="absolute"
                      right="2"
                      top="50%"
                      transform="translateY(-50%)"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <LuEyeOff size={20} /> : <LuEye size={20} />}
                    </IconButton>
                  </Box>
                </Field.Root>

                <Button
                  type="submit"
                  colorPalette="teal"
                  size="xl"
                  width="100%"
                  borderRadius="2xl"
                  fontWeight="bold"
                  height="60px"
                  mt={2}
                  loading={loginMutation.isPending}
                  _hover={{ transform: "translateY(-2px)" }}
                  transition="all 0.2s"
                >
                  Sign In
                </Button>
              </Stack>
            </Box>

            <Text textAlign="center" fontSize="md" color="gray.500">
              New here?{" "}
              <ChakraLink
                color="teal.600"
                fontWeight="bold"
                onClick={() => router.push("/signup")}
                _hover={{ textDecoration: "underline", cursor: "pointer" }}
              >
                Create an account
              </ChakraLink>
            </Text>
          </Stack>
        </Box>
      </MotionBox>
    </Flex>
  );
}